      >
        <el-option
          v-for="item in options"
          :key="item.id"
          :label="item.stuName"
          :value="item.id"
        />
      </el-select>
      <span></span>
      <el-select
        v-model="subject"
        placeholder=""
        filterable
        @change="change"
      >
        <el-option
          v-for="item in subjectOptions"
          :key="item.id"
          :label="item.major"
          :value="item.id"
        />
      </el-select>
    </div>
    <el-table
      v-loading="loading"
      :data="tableData"
      style="width: 100%;margin-top: 20px"
    >
      <el-table-column
        fixed
        align="center"
        prop="subject.subject"
        label=""
        width="100"
      />
      <el-table-column
        show-overflow-tooltip
        align="center"
        width="160"
        label=""
        prop="classname"
      />
      <el-table-column
        show-overflow-tooltip
        align="center"
        width="180"
        label=""
      >
        <template scope="scope">
          {{ new Date(scope.row.min_age).getFullYear()-1 }}
          {{ new Date(scope.row.min_age).getMonth()+1 }}
          {{ new Date(scope.row.min_age).getDate() }}---
          {{ new Date(scope.row.min_age).getFullYear() }}
          {{ new Date(scope.row.min_age).getMonth()+1 }}
          {{ new Date(scope.row.min_age).getDate() }}
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        width="180"
        label=""
      >
        <template scope="scope">
          <span v-if="scope.row.classroominfo.day===1">
            
          </span>
          <span v-if="scope.row.classroominfo.day===2">
            
          </span>
          <span v-if="scope.row.classroominfo.day===3">
            
          </span>
          <span v-if="scope.row.classroominfo.day===4">
            
          </span>
          <span v-if="scope.row.classroominfo.day===5">
            
