      />
      <el-table-column
        align="center"
        width="180"
        label=""
      >
        <template slot-scope="scope">
          <!-- {{ scope.row.kaifang }} =1
          {{ scope.row.subject.interview }} =1
          {{ scope.row.infoFlag }} =0
          {{ scope.row.num }}>0 -->
          <el-button
            size="mini"
            type="primary"
            v-if="scope.row.kaifang===1 && scope.row.infoFlag ===0 &&scope.row.num >0"
            @click="handleEdit(scope.row.id)"
          >
            
          </el-button>
          <el-button
            size="mini"
            type="primary"
            v-if="scope.row.subject.interview === 1&&
              scope.row.kaifang===1 &&
              scope.row.infoFlag ===0 &&
              scope.row.num >0"
            @click="handleInterview"
          >
            
          </el-button>
          <el-button
            size="mini"
            disabled
            v-if="
              scope.row.kaifang===0 ||
                scope.row.infoFlag ===1 ||
                scope.row.num <=0"
          >
            
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <div style="margin-top: 20px;cursor:pointer;">
      <span @click="why">
        <i class="el-icon-info" />?
      </span>
      <el-pagination
        class="page"
        background
        layout="prev, pager, next"
        :total="total"
        @current-change="handleCurrentChange"
      />
    </div>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="confirm"
      width="30%"
    >
      <span>
        ?<br>
        
