        </p>
        <h2><br></h2>
        <h3 style="color:red">
          
        </h3>
      </div>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button
          type="primary"
          @click="Whysel = false"
        >
           
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import MInfo from './Info.vue';

export default {
  data() {
    return {
      // 
      success: false,
      // 
      confirm: false,
      // 
      Whysel: false,
      userId: '',
      options: [],
      value: '',
      subject: '',
      subjectId: '',
      subjectOptions: [],
      loading: true,
      tableData: [],
      page: 1,
      pages: 1,
      header: '',
      message: '',
      total: 1,
    };
  },
  mounted() {
    this.userId = this.$Storage.get('userId');
    if (this.userId === '' || this.userId === undefined) {
      this.$router.push('/login');
    } else {
      this.getStu();
      this.getSubject();
    }
  },
  methods: {
    why() {
      this.Whysel = true;
    },
    // 
    getStu() {
      this.$http({
        methods: 'POST',
        url: '/pjsys/TeachingSystem/findById.action',
        params: {
          user_id: this.userId,
          page: this.pages,
        },
      })
        .then((res) => {
          this.options = res.data;
          this.loading = false;
        })
        .catch(() => {
          this.loading = false;
          this.$message({
            message: '',
            type: 'error',
          });
        });
    },
    // 
    getSubject() {
      this.$http({
        methods: 'POST',
        url: '/pjsys/findAllMajors.action',
        params: {
          user_id: this.userId,
          page: this.pages,
        },
      })
        .then((res) => {
          this.subjectOptions = res.data;
          this.loading = false;
        })
        .catch(() => {
          this.loading = false;
          this.$message({
            message: '',
            type: 'error',
          });
        });
    },
    // 
    change() {
      this.getNotice(this.value, this.subject);
    },
    // 
    getNotice(stuId, major, page = 1) {
      this.$http({
        methods: 'POST',
        url: '/pjsys/findBySubjectAndAge.action',
        params: {
          stu_id: stuId,
          major_id: major,
          page,
        },
      })
        .then((res) => {
          this.tableData = res.data.datas;
          this.total = Number(res.data.total);
        })
        .catch(() => {
          this.$message({
            message: '',
            type: 'error',
          });
        });
    },
    // 
    handleEdit(id) {
      this.confirm = true;
      this.subjectId = id;
    },
    // 
    handleInterview() {
      this.$router.push(`/main/selinterview/${this.value}`);
    },
    // 
    handleCurrentChange(page) {
      this.getNotice(this.value, this.subject, page);
    },
    // 
    info(message, type) {
      this.$message({
        message,
        type,
      });
    },
    // 
    confirmApply() {
      this.confirm = false;
      this.$http({
        methods: 'POST',
        url: '/pjsys/selectSubject.action',
        params: {
          stu_id: this.value,
          classinfo_id: this.subjectId,
        },
      })
        .then((res) => {
          if (res.data === 0) {
            this.info('', 'error');
          } else if (res.data === 1) {
            this.success = true;
            this.getNotice(this.value, this.subject);
          } else if (res.data === 2) {
            this.info('', 'error');
          } else if (res.data === 3) {
            this.info(',,', 'error');
          } else {
            this.info(',', 'error');
          }
        });
    },
  },
  components: {
    MInfo,
  },
};
</script>

<style scoped>
.page{
  float: right
}
