<template>
  <div>
    <p class="red">
      :,,,
    </p>
    <el-form
      :model="ruleForm2"
      status-icon
      :rules="rules2"
      ref="ruleForm2"
      label-width="100px"
      style="width:400px"
    >
      <el-form-item
        prop="name"
        label=""
      >
        <el-input
          v-model="ruleForm2.name"
          class="inputWidth"
          placeholder=""
          autocomplete="off"
        />
      </el-form-item>
      <el-form-item
        prop="pass"
        label=""
      >
        <el-radio
          v-model="ruleForm2.sex"
          label="1"
        >
          
        </el-radio>
        <el-radio
          v-model="ruleForm2.sex"
          label="2"
        >
          
        </el-radio>
      </el-form-item>
      <el-form-item
        prop="idNum"
        label=""
      >
        <el-input
          v-model="ruleForm2.idNum"
          placeholder=""
          autocomplete="off"
        />
      </el-form-item>
      <el-form-item
        prop="school"
        label=""
      >
        <el-input
          v-model="ruleForm2.school"
          placeholder=""
          autocomplete="off"
        />
      </el-form-item>
      <el-form-item
        prop="address"
        label=""
      >
        <el-input
          v-model="ruleForm2.address"
          placeholder=""
          autocomplete="off"
        />
      </el-form-item>
      <el-form-item
        prop="code"
        label=""
      >
        <el-input
          v-model="ruleForm2.code"
          placeholder=""
          autocomplete="off"
        />
      </el-form-item>
      <el-form-item
        prop="familyName"
        label=""
      >
        <el-input
          v-model="ruleForm2.familyName"
          placeholder=""
          autocomplete="off"
        />
      </el-form-item>
      <el-form-item
        prop="familyTel"
        label=""
      >
        <el-input
          disabled
          v-model="ruleForm2.familyTel"
          placeholder=""
          autocomplete="off"
        />
      </el-form-item>
      <el-form-item
        prop="spareName"
        label=""
      >
        <el-input
          v-model="ruleForm2.spareName"
          placeholder=""
          autocomplete="off"
        />
      </el-form-item>
      <el-form-item
        prop="spareTel"
        label=""
      >
        <el-input
          v-model="ruleForm2.spareTel"
          placeholder=""
          autocomplete="off"
        />
      </el-form-item>
      <el-form-item
        label=""
      >
        <el-input
          v-model="ruleForm2.unit"
          placeholder=""
          autocomplete="off"
        />
      </el-form-item>
      <el-form-item>
        <el-button
          class="submit"
          type="primary"
          @click="submitForm('ruleForm2')"
        >
          
        </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    // 
    const validateName = (rule, value, callback) => {
      const myreg = /^[\u0391-\uFFE5]+$/;
      if (value === '') {
        callback(new Error(''));
      } else if (!myreg.test(value)) {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    // 
    const validateidNum = (rule, value, callback) => {
      const myreg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
      if (value === '') {
        callback(new Error(''));
      } else if (!myreg.test(value)) {
        callback(new Error('1518'));
      } else {
        callback();
      }
    };
    // 
    const validateSchool = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    // 
    const validateAddress = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    // 
    const validateCode = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    // 
    const validateFamilyName = (rule, value, callback) => {
      const myreg = /^[\u0391-\uFFE5]+$/;
      if (value === '') {
        callback(new Error(''));
      } else if (!myreg.test(value)) {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    // 
    const validateFamilyTel = (rule, value, callback) => {
      const myreg = /^[1][0-9]{10}$/;
      if (value === '') {
        callback(new Error(''));
      } else if (!myreg.test(value)) {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    // 
    const validateSpareName = (rule, value, callback) => {
      const myreg = /^[\u0391-\uFFE5]+$/;
      if (value === '') {
        callback(new Error(''));
      } else if (!myreg.test(value)) {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    // 
    const validateSpareTel = (rule, value, callback) => {
      const myreg = /^[1][0-9]{10}$/;
      if (value === '') {
        callback(new Error(''));
      } else if (!myreg.test(value)) {
        callback(new Error(''));
      } else {
        callback();
      }
    };
    return {
      ruleForm2: {
        addShow: true,
        name: '',
        sex: '1',
        idNum: '',
        school: '',
        address: '',
        code: '',
        familyName: '',
        familyTel: '',
        spareName: '',
        spareTel: '',
        unit: '',
      },
      rules2: {
        name: [
          { required: true, validator: validateName, trigger: 'blur' },
        ],
        idNum: [
          { required: true, validator: validateidNum, trigger: 'blur' },
        ],
        school: [
          { required: true, validator: validateSchool, trigger: 'blur' },
        ],
        address: [
          { required: true, validator: validateAddress, trigger: 'blur' },
        ],
        code: [
          { required: true, validator: validateCode, trigger: 'blur' },
        ],
        familyName: [
          { required: true, validator: validateFamilyName, trigger: 'blur' },
        ],
        familyTel: [
          { required: true, validator: validateFamilyTel, trigger: 'blur' },
        ],
        spareName: [
          { required: true, validator: validateSpareName, trigger: 'blur' },
        ],
        spareTel: [
          { required: true, validator: validateSpareTel, trigger: 'blur' },
        ],
      },
    };
  },
  mounted() {
    this.$http({
      methods: 'POST',
      url: '/pjsys/findById1.action',
      params: {
        id: this.$route.params.id,
      },
    })
      .then((res) => {
        // eslint-disable-next-line no-console
        console.log(res.data);
        this.ruleForm2.sex = String(res.data[0].sex);
        this.ruleForm2.name = res.data[0].stuName;
        this.ruleForm2.idNum = res.data[0].personalNum;
        this.ruleForm2.school = res.data[0].schoolname;
        this.ruleForm2.address = res.data[0].address;
        this.ruleForm2.code = res.data[0].postcode;
        this.ruleForm2.familyName = res.data[0].parent;
        this.ruleForm2.familyTel = res.data[0].mobilephone;
        this.ruleForm2.spareName = res.data[0].emergencyContactName;
        this.ruleForm2.spareTel = res.data[0].emergencyContactPhone;
        this.ruleForm2.unit = res.data[0].workaddress;
      })
      .catch(() => {
        this.$message({
          message: ',',
          type: 'error',
        });
      });
  },
  methods: {
    submitForm(formName) {
      // eslint-disable-next-line consistent-return
      this.$refs[formName].validate((valid) => {
        if (valid) {
          const userId = this.$Storage.get('userId');
          // eslint-disable-next-line prefer-destructuring
          const id = this.$route.params.id;
          // eslint-disable-next-line no-console
          console.log(this.ruleForm2);
          this.$http({
            methods: 'POST',
            url: '/pjsys/update.action',
            params: {
              user_id: userId,
              id,
              stuName: this.ruleForm2.name,
              sex: this.ruleForm2.sex,
              personalNum: this.ruleForm2.idNum,
              schoolname: this.ruleForm2.school,
              Address: this.ruleForm2.address,
              postcode: this.ruleForm2.code,
              parent: this.ruleForm2.familyName,
              mobilephone: this.ruleForm2.familyTel,
              EmergencyContactName: this.ruleForm2.spareName,
              EmergencyContactPhone: this.ruleForm2.spareTel,
              workaddress: this.ruleForm2.unit,
            },
          })
            .then((res) => {
              if (res.data === 1) {
                this.$message({
                  message: '',
                  type: 'success',
                });
                this.$router.push('/main/info');
              } else if (res.data === 0) {
                this.$message({
                  message: '',
                  type: 'error',
                });
              } else {
                this.$message({
                  message: '',
                  type: 'error',
                });
              }
            })
            .catch(() => {
              this.$message({
                message: '',
                type: 'error',
              });
            });
        } else {
          this.$message({
            message: '',
            type: 'error',
          });
          return false;
        }
      });
    },
  },
};
</script>

