<template>
  <div>
    <el-table
      :data="stuList"
      style="width: 100%"
      v-loading="loading"
      :row-class-name="tableRowClassName"
    >
      <el-table-column
        fixed
        show-overflow-tooltip
        prop="student.stuName"
        label=""
        align="center"
        width="100"
      />
      <el-table-column
        show-overflow-tooltip
        prop="interview_timeslot.interview.interviewSubject"
        align="center"
        width="100"
        label=""
      />
      <el-table-column
        show-overflow-tooltip
        prop="interview_timeslot.starttime"
        align="center"
        width="240"
        label=""
      />
      <el-table-column
        show-overflow-tooltip
        width="240"
        prop="interview_timeslot.end_time"
        align="center"
        label=""
      />
      <el-table-column
        label=""
        align="center"
        :formatter="formatterColumn"
      >
        <!-- <template scope="scope">
          {{ scope.row.sex === 1 ? '': '' }}
        </template> -->
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      user_id: '',
      stuList: [],
      loading: true,
    };
  },
  mounted() {
    this.user_id = this.$Storage.get('userId');
    if (this.user_id === '' || this.user_id === undefined) {
      this.$router.push('/login');
    } else {
      this.getStuList();
    }
  },
  methods: {
    getStuList() {
      this.$http({
        methods: 'POST',
        url: '/pjsys/pisys/findInterviewList',
        params: {
          user_id: this.user_id,
        },
      })
        .then((res) => {
          this.stuList = res.data;
          this.loading = false;
        })
        .catch(() => {
          this.loading = false;
        });
    },
    // 
    tableRowClassName() {
    // tableRowClassName({ row }) {
      // if (row.result === 1) {
      //   return 'success';
      // }
      // if (row.result === 2) {
      //   return 'err';
      // }
      return '';
    },
    // 
    formatterColumn(row) {
      switch (row.result) {
        case 0:
          return ',';
        case 1:
          return ',';
        case 2:
          return ',';
        default:
          return '';
      }
    },
  },
};
</script>

