<template>
  <div>
    <el-table
      :data="stuList"
      style="width: 100%"
      v-loading="loading"
    >
      <el-table-column
        fixed
        show-overflow-tooltip
        prop="student.stuName"
        label=""
        align="center"
        width="100"
      />
      <el-table-column
        show-overflow-tooltip
        prop="classinfo.subject.subject"
        align="center"
        width="100"
        label=""
      />
      <el-table-column
        show-overflow-tooltip
        prop="classinfo.classname"
        align="center"
        width="140"
        label=""
      />
      <el-table-column
        show-overflow-tooltip
        width="180"
        align="center"
        label=""
      >
        <template scope="scope">
          <span v-if="scope.row.classinfo.classroominfo.day===1">
            
          </span>
          <span v-if="scope.row.classinfo.classroominfo.day===2">
            
          </span>
          <span v-if="scope.row.classinfo.classroominfo.day===3">
            
          </span>
          <span v-if="scope.row.classinfo.classroominfo.day===4">
            
          </span>
          <span v-if="scope.row.classinfo.classroominfo.day===5">
            
          </span>
          <span v-if="scope.row.classinfo.classroominfo.day===6">
            
          </span>
          <span v-if="scope.row.classinfo.classroominfo.day===7">
            
          </span>
          {{ scope.row.classinfo.classroominfo.start }}---
          {{ scope.row.classinfo.classroominfo.jieshu }}
        </template>
      </el-table-column>
      <el-table-column
        show-overflow-tooltip
        width="60"
        prop="classinfo.keshi"
        align="center"
        label=""
      />
      <el-table-column
        show-overflow-tooltip
        width="60"
        prop="classinfo.money"
        align="center"
        label=""
      />
      <el-table-column
        show-overflow-tooltip
        width="210"
        label=""
        align="center"
        :formatter="formatterColumn"
      />
      <el-table-column
        align="center"
        label=""
      >
        <template slot-scope="scope">
          <el-button
            v-if="scope.row.flag==1"
            size="mini"
            type="primary"
            @click="Payment(scope.row.id,2,',,!')"
          >
            
          </el-button>
          <el-button
            v-else-if="scope.row.flag==7"
            size="mini"
            type="primary"
            @click="Payment(scope.row.id,8,',,!')"
          >
            
          </el-button>
          <span v-else>
            
          </span>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      user_id: '',
      stuList: [],
      loading: true,
    };
  },
  mounted() {
    this.user_id = this.$Storage.get('userId');
    if (this.user_id === '' || this.user_id === undefined) {
      this.$router.push('/login');
    } else {
      this.getStuList();
    }
  },
  methods: {
    // 
    getStuList() {
      this.$http({
        methods: 'POST',
        url: '/pjsys/pisys/findClassinfo.action',
        params: {
          user_id: this.user_id,
        },
      })
        .then((res) => {
          this.stuList = res.data;
          this.loading = false;
        })
        .catch(() => {
          this.loading = false;
        });
    },
    Payment(id, flag, info) {
      const This = this;
      this.$http({
        method: 'POST',
        url: '/pjsys/pay.action',
        params: {
          ids: id,
          flag,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            This.info(info, 'success');
            this.getStuList();
          } else {
            This.info(',!', 'error');
          }
        });
    },
    // 
    info(message, type) {
      this.$message({
        message,
        type,
      });
    },
    // 
    formatterColumn(row) {
      switch (row.flag) {
        case 0:
          return ',';
        case 1:
          return ',';
        case 2:
          return ',';
        case 3:
          return '';
        case 4:
          return '';
        case 5:
          return ',';
        case 6:
          return '';
        case 7:
          return ',';
        case 8:
          return ',';
        case 9:
          return '';
        case 10:
          return '';
        case 11:
          return '';
        case 12:
          return '';
        default:
          return '';
      }
    },
  },
};
</script>

