<template>
  <div>
    <el-table
      :data="stuList"
      style="width: 100%"
      v-loading="loading"
    >
      <el-table-column
        fixed
        show-overflow-tooltip
        prop="stuName"
        label=""
        align="center"
        width="100"
      />
      <el-table-column
        label=""
        align="center"
        width="70"
      >
        <template scope="scope">
          {{ scope.row.sex === 1 ? '': '' }}
        </template>
      </el-table-column>
      <el-table-column
        show-overflow-tooltip
        prop="birthday"
        align="center"
        width="100"
        label=""
      />
      <el-table-column
        show-overflow-tooltip
        prop="personalNum"
        align="center"
        width="180"
        label=""
      />
      <el-table-column
        show-overflow-tooltip
        width="180"
        prop="address"
        align="center"
        label=""
      />
      <el-table-column
        show-overflow-tooltip
        prop="parent"
        align="center"
        width="100"
        label=""
      />
      <el-table-column
        show-overflow-tooltip
        prop="mobilephone"
        align="center"
        width="120"
        label=""
      />
      <el-table-column
        prop="mobilephone"
        align="center"
        width="180"
        label=""
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            @click="handleEdit(scope.row.id)"
          >
            
          </el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.row.id)"
          >
            
          </el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      user_id: '',
      stuList: [],
      loading: true,
    };
  },
  mounted() {
    this.user_id = this.$Storage.get('userId');
    if (this.user_id === '' || this.user_id === undefined) {
      this.$router.push('/login');
    } else {
      this.getStuList();
    }
  },
  methods: {
    getStuList() {
      this.$http({
        methods: 'POST',
        url: '/pjsys/findById.action',
        params: {
          user_id: this.user_id,
        },
      })
        .then((res) => {
          this.stuList = res.data;
          this.loading = false;
        })
        .catch(() => {
          this.loading = false;
        });
    },
    handleEdit(id) {
      this.$router.push(`/main/upinfo/${id}`);
    },
    handleDelete(id) {
      this.$http({
        methods: 'POST',
        url: '/pjsys/delete.action',
        params: {
          id,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.$message({
              message: '',
              type: 'success',
            });
            this.getStuList();
          } else {
            this.$message({
              message: '!',
              type: 'error',
            });
          }
        })
        .catch(() => {
          this.$message({
            message: ',',
            type: 'error',
          });
        });
    },
  },
};
</script>

