// 

<template>
  <div>
    <el-table
      v-loading="loading"
      :data="tableData"
      :row-class-name="tableRowClassName"
      style="width: 100%"
      @select="change"
      @select-all="change"
    >
      <el-table-column
        align="center"
        type="selection"
        width="60"
      />
      <el-table-column
        align="center"
        prop="head"
        label=""
        width="180"
      />
      <el-table-column
        align="center"
        label=""
        prop="message"
      >
        <template
          slot-scope="scope"
        >
          <span
            v-html="scope.row.message"
          />
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        prop="time"
        width="180"
        label=""
      />
      <el-table-column
        align="center"
        width="80"
        label=""
      >
        <template scope="scope">
          {{ scope.row.flag === 1 ? '': '' }}
        </template>
      </el-table-column>
      <el-table-column
        align="center"
        width="240"
        label=""
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            @click="handleEdit(scope.row.id)"
          >
            
          </el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.row.id)"
          >
            
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="bottom">
      <el-button
        size="mini"
        type="danger"
        @click="selDelete"
      >
        
      </el-button>
      <!--  -->
      <el-pagination
        class="page"
        background
        layout="prev, pager, next"
        :total="total"
        :current-page.sync="currentPage1"
        @current-change="handleCurrentChange"
      />
    </div>

    <!--  -->
    <el-dialog
      :title="header"
      :visible.sync="dialogVisible"
      width="30%"
    >
      <div v-html="message " />
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button
          type="primary"
          @click="dialogVisible = false"
        >
           
        </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialogVisible: false,
      // loading
      loading: true,
      // 
      total: 1,
      // 
      header: '',
      // 
      message: '',
      // id
      userId: '',
      // 
      tableData: [],
      // 
      currentPage1: 2,
      // id
      ids: [],
    };
  },
  mounted() {
    this.userId = this.$Storage.get('userId');
    if (this.userId === '' || this.userId === undefined) {
      this.$router.push('/login');
    } else { this.getList(); }
  },
  beforeRouteUpdate(to, from, next) {
    next();
    this.getList();
  },
  methods: {
    tableRowClassName({ row }) {
      if (row.flag === 1) {
        return 'read-row';
      }
      return '';
    },
    getList() {
      this.$http({
        methods: 'POST',
        url: '/pjsys/findMessage.action',
        params: {
          user_id: this.userId,
          page: this.$route.params.id,
        },
      })
        .then((res) => {
          this.tableData = res.data.datas;
          this.total = res.data.total;
          this.loading = false;
          this.currentPage1 = Number(this.$route.params.id);
        })
        .catch(() => {
          this.$message({
            message: ',',
            type: 'error',
          });
        });
    },
    // 
    handleEdit(id) {
      // 
      this.$http({
        methods: 'POST',
        url: '/pjsys/XQMessage.action',
        params: {
          id,
        },
      })
        .then((res) => {
          this.dialogVisible = true;
          this.header = res.data.head;
          this.message = res.data.message;
        })
        .catch(() => {
          this.$message({
            message: '',
            type: 'error',
          });
        });
      // 
      this.$http({
        methods: 'POST',
        url: '/pjsys/updateFlagMessage.action',
        params: {
          id,
        },
      })
        .then(() => {
          this.getList();
        })
        .catch(() => {
          this.$message({
            message: '',
            type: 'error',
          });
        });
    },
    // 
    handleDelete(id) {
      this.$http({
        methods: 'POST',
        url: '/pjsys/deleteMessage.action',
        params: {
          id,
        },
      })
        .then((res) => {
          if (res.data === 1) {
            this.$message({
              message: '',
              type: 'success',
            });
            this.getList();
          } else {
            this.$message({
              message: '',
              type: 'error',
            });
          }
        })
        .catch(() => {
          this.$message({
            message: '',
            type: 'error',
          });
        });
    },
    // 
    handleCurrentChange(id) {
      this.$router.push(`/main/all/${id}`);
    },
    // 
    change(selection) {
      this.ids = selection.map(res => res.id);
    },
    // 
    selDelete() {
      this.$http({
        method: 'POST',
        url: '/pjsys/deleteAll.action',
        params: {
          ids: this.ids.toString(),
        },
      })
        .then((res) => {
          // eslint-disable-next-line no-console
          console.log(res.data);
          if (res.data === 1) {
            this.$message.success('');
            this.getList();
          } else {
            this.$message.error('');
          }
        });
    },
  },
};
</script>

<style lang="stylus" scoped>
.bottom
  margin-top 20px
  .page
    float right
</style>
